<template>
	<view class="content">
		<view class="suggest">
			<view class="uni-textarea"><textarea class="textarea" maxlength="length" v-model="msg" placeholder-style="color:#ccc" :placeholder="placeholder" /></view>
			<view class="computeNum">{{ computeLength }}</view>

			<view style="padding: 30rpx;">
				<uImg
					ref="upimg"
					:canUploadFile="true"
					:limit="limitNum"
					:uploadFileUrl="uploadFileUrl"
					:heaer="header"
					:fileKeyName="name"
					:uImgList.sync="uImgList"
					@uploadSuccess="uploadSuccess"
				></uImg>
			</view>
		</view>

		<button @click="upFile">上传</button>
	</view>
</template>

<script>
import uImg from '../../components/newOne.vue';
let toast = msg => {
	uni.showToast({
		title: msg,
		icon: 'none'
	});
};

export default {
	components: { uImg },

	data() {
		return {
			msg: '',
			length: 140,
			placeholder: '欢迎反馈任何意见和问题,您的反馈也是我们产品的动力哦',
			limitNum: 6,
			uploadFileUrl: '', //替换成你的后端接收文件地址
			name: '', //上传的名字
			header: {
				// 如果需要header，请上传
			},
			uImgList: ['/static/logo.png']
		};
	},
	computed: {
		computeLength() {
			return (this.length = 140 - this.msg.length);
		}
	},
	methods: {
		uploadSuccess(result) {
			if (result.statusCode == 200) {
				//上传成功的回调处理
				toast('上传成功');
			} else {
				toast('上传失败');
			}
		},
		//上传方法的调用
		upFile() {
			this.$refs.upimg.upload();
		}
	}
};
</script>

<style></style>
